export default {
  data() {
    return {
      list: [
        // {
        //   title: '眼部检查主观',
        //   id: 0
        // },
        // {
        //   title: '眼部检查客观',
        //   id: 1
        // },
        {
          title: '眼压',
          id: 0
        },
        {
          title: '眼部生物学参数检查',
          id: 1
        },
        {
          title: '泪液检查',
          id: 2
        }
      ]
    }
  },
  methods: {
    handleClick(id) {
      var url = null
      switch (id) {
        case 0:
          url = 'eye_objective_Int_pressure' // 眼压
          break
        case 1:
          url = 'eye_objective_Bio_parameters' // 眼部生物学参数
          break
        case 2:
          url = 'eye_objective_tear' // 泪液检查
          break
      }
      this.$router.push({ name: url, query: { data: JSON.stringify() }})
    }
  }
}



